import axios from 'axios';
import { useFormik } from 'formik';
import React, { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import toast from 'react-hot-toast';
import * as yup from 'yup'
import Note from '../Note/Note';
import { Swal } from 'sweetalert2';
import img from '../../image/503-5038585_clip-art-take-note-note-taking-png-transparent.png'
import { Link, useNavigate } from 'react-router-dom';
export default function Home() {

  let [newList,setNewList]=useState([])

  useEffect(()=>{
    displaynote()
  },[])

 

  let[noteList,setNoteList]=useState([])

  const [show, setShow] = useState(false);
 

  const handleClose = () => setShow(false);
 
  const handleShow = () => setShow(true);
  let[error,seterror]=useState(false)
 

  let validationSchema= yup.object({
       
    title:yup.string().required('title is required'),
 
   content:yup.string().required('contant is required')

})



//addnote
  async function addNotes(val){
console.log(val)
try {
  let {data}= await axios.post(`https://note-sigma-black.vercel.app/api/v1/notes`,val ,{
    headers:{
    token:`3b8ny__${localStorage.getItem('token')}`
    }
  })
 
  console.log(data)
  if(data.msg=='done'){
    toast.success('Note Added Successufly')
    displaynote()
  }
  
} catch (error) {
  console.log(error)
  
}
  }

  //displaynote
  

 async function displaynote(){

  try {

    let {data}= await axios.get(`https://note-sigma-black.vercel.app/api/v1/notes`,{
      headers:{
        token:`3b8ny__${localStorage.getItem('token')}`
      }
    })

    console.log(data)
    if(data.msg=='done'){
      seterror(false)
      setNoteList(data.notes)
      setNewList(data.notes)
    }
    
  } catch (error) {
    console.log(error)
    seterror(true)
    
    
  }
    
  }




  console.log(newList)



  let formik = useFormik({
    initialValues:{
      title:'',
      content:'',
    },
    validationSchema,
    onSubmit:addNotes,

  })

let nav=useNavigate()
  function logout(){
    localStorage.removeItem('token')
    nav('/login')

  }

  


  function search(val){
    let newnote= newList.filter((ele)=> ele.title.toLowerCase().includes(val.toLowerCase())==true)
      setNoteList(newnote)
    
  }


  return <>

<div className='navv'>

<nav className="navbar navbar-expand-lg ">
  <div className="container">
    <a className="navbar-brand text-light" href="#">
    <img src={img} className='noteimg rounded-5' alt="img" />
    </a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
  
    
    
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0 gap-4">
      <li className='nav-item'>
      <form className="d-flex mx-auto " role="search">
      <input onChange={(e)=>search(e.target.value)} type="text" className='form-control' placeholder='Search' />
        
      </form>
      </li>
       
        <li className="nav-item ">
        <button onClick={handleShow} className='btn btn-main d-block w-100 mx-auto text-light '>Add Note</button>
        </li>
        <li className="nav-item">
        <button onClick={logout} className='btn btn-main d-block mx-auto w-100  text-light'>Logout</button>
        </li>
        
        
      </ul>
      
    </div>
  </div>
</nav>

</div>

<Modal show={show} onHide={handleClose}  size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered>
        <Modal.Header closeButton>
          <Modal.Title>Add Note</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form action="">
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-4' type="text" name='title' id='title' placeholder='Enter Title' />
            {formik.errors.title && formik.touched.title ? <p>{formik.errors.title}</p>:''}
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control' type="text" name='content' id='content' placeholder='Enter Content'/>
            {formik.errors.contant && formik.touched.contant ? <p>{formik.errors.contant}</p>:''}
          </form>

        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={function(){
            handleClose()
            formik.handleSubmit()
          }}>
            Add Note
          </Button>
        </Modal.Footer>
      </Modal>

    
   <div className='container'>
    <div className='row'>
    <h1 className=''>
    <i className="fa-solid fa-folder me-3"></i>
      My Notes</h1>
    <div className='empty'></div>
    
    <div className='col-md-12 '>
    <div className='row mx-4'>
     {error !=true ? noteList.map((note)=><Note key={note._id} note={note} displaynote={displaynote} />
      
      ):<div className=' bg-danger text-light text-center py-5 fs-2 rounded-5'>not notes found</div> }
    
    </div>
    </div>

   </div>
    </div>
   
   
     


      
   

     
  

     
      

     
  
  
  
  </>
}
