import logo from './logo.svg';
import { Toaster } from 'react-hot-toast';
import './App.css';
import {RouterProvider, createBrowserRouter, createHashRouter} from 'react-router-dom'
import Layout from './Components/Layout/Layout';
import Home from './Components/Home/Home';
import Register from './Components/Register/Register';
import Login from './Components/Login/Login';
import NoutFound from './Components/NotFound/NoutFound';
import ProtectedRouting from './Components/ProtectedRouting/ProtectedRouting';

function App() {

  const routes=createHashRouter([{
    path:'/' ,element: <Layout/> ,children: [
      {index:true ,element: <ProtectedRouting><Home/></ProtectedRouting> },
      {path:'/register',element:<Register/>},
      {path:'/login',element:<Login/>},
      {path:'*',element:<NoutFound/>},
  ]
  }])


  return <>
  <Toaster />

  <RouterProvider router={routes}>
  
  </RouterProvider>


  
  
  </>
}

export default App;
