import React, { useState } from 'react'
import {useFormik} from 'formik'
import * as yup from 'yup'
import axios from 'axios'
import { Link , useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import img from '../../image/a40dff179144e07fc168e6e6dabcf746.jpg'
export default function Register() {
    let nav= useNavigate()
    let [error,setError]=useState('')
    let [loading,setloading]=useState(false)


   async function handleRegister(value){
    setloading(true)
       
        let {data}= await axios.post(`https://note-sigma-black.vercel.app/api/v1/users/signUp`,value).catch((err)=>{setError(err.response.data.msg)
        Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: err.response.data.msg,
           
          })
        setloading(false)
   })
        console.log(data)
        if(data.msg=='done'){
            Swal.fire({
                icon: 'success',
                title: 'successuflly',
                text: 'your register is successfully',
               
              })
            setloading(false)
            
            nav('/login')

        }
    }

      //yup
      let validationSchema= yup.object({
        name:yup.string().max(15,'max length is 15').min(3,'min length is 3').required('name is required'),
         email:yup.string().email('email is invalid').required('email is required'),
        phone:yup.string().required('phone is required').matches(/^01[0125][0-9]{8}$/,'phone is invalid '),
        password:yup.string().required('password is required').max(15,'max length is 15').min(3,'min length is 3'),
       age:yup.number().required('age is required')
    })
        //formik
    const formik=useFormik({
        initialValues:{
            name:'',
            email:'',
            password:'',
            age:'',
            phone:'',
        },

         validationSchema,

        onSubmit:handleRegister,
    })



  return <>


  {/* <div className='container py-5 vh-100 mt-5  '>
    <div className='row g-0 h-75  '>
        <div className='col-md-4  '>
            <div className=' item text-center h-100 d-flex flex-column justify-content-center align-items-center'>
                <img className='w-50 mb-4 rounded-circle' src={img} alt="" />
                <h2 className='text-light mb-4'>Welcome Back !</h2>
                <h5 className='text-light  mb-4'>To Keep Conected With Us Please Login With Your Personal Information </h5>
                <Link className='btn btn-outline-light' to='/login'>Login</Link>
            </div>

        </div>
        <div className='col-md-8 '>

           <div className='item-info h-100 d-flex flex-column justify-content-center align-items-center '>
           <h2 className='text-center text-warning'>Create Account</h2>

        {error=='' ? '' : <p className='alert alert-danger text-center w-75 mx-auto'>{error}</p>}
           <form onSubmit={formik.handleSubmit} action="" className='w-75 mx-auto'>
           <input onChange={formik.handleChange} onBlur={formik.handleBlur} placeholder='Enter Your Name' className='form-control mt-3' name='name' type="text" />

           {formik.errors.name && formik.touched.name ? <p className='alert alert-danger'>{formik.errors.name}</p> : ''}


           <input onChange={formik.handleChange} onBlur={formik.handleBlur}  placeholder='Enter Your Email' className='form-control mt-3' name='email' type="email" />

           {formik.errors.email && formik.touched.email ? <p className='alert alert-danger'>{formik.errors.email}</p> : ''}


           <input onChange={formik.handleChange} onBlur={formik.handleBlur}  placeholder='Enter Your Password' className='form-control mt-3' name='password' type="password" />

           {formik.errors.password && formik.touched.password ? <p className='alert alert-danger'>{formik.errors.password}</p> : ''}
           

           <input onChange={formik.handleChange} onBlur={formik.handleBlur}  placeholder='Enter Your Age' className='form-control mt-3' name='age' type="number" />
           {formik.errors.age && formik.touched.age ? <p className='alert alert-danger'>{formik.errors.age}</p> : ''}

           <input onChange={formik.handleChange} onBlur={formik.handleBlur}  placeholder='Enter Your Phone' className='form-control mt-3' name='phone' type="tel" />
            {formik.errors.phone && formik.touched.phone ? <p className='alert alert-danger'>{formik.errors.phone}</p> : ''}

            
            <button type='submit' className='btn btn-warning w-100 my-3 text-light'>
                {loading ? <i className='fa-solid fa-spinner fa-spin'></i> : 'Register' }


            </button>

           </form>
           </div>

        </div>
    </div>
  </div> */}

<section className="min-vh-100 d-flex align-items-center justify-content-center">
      <div className='container row g-3'>
        <figure className="col-md-6 m-0 p-md-0 ">
          <div className="image-container text-center py-5 rounded-4">
          
            <img src={img} className="w-50 rounded-circle" alt="Regsiter Image" />
            <h2 className='text-light mb-4'>Welcome Back !</h2>
          <h5 className='text-light  mb-4'>To Keep Conected With Us Please Login With Your Personal Information </h5>
          <Link to="/login" className="text-decoration-underline btn btn-main">
                Log in
              </Link>
          </div>
        </figure>
        <form onSubmit={formik.handleSubmit} className="col-md-6 d-flex flex-column justify-content-center px-5">
        {error=='' ? '' : <p className='alert alert-danger text-center w-75 mx-auto'>{error}</p>}
          <h2 className="m-0 fw-bold font-Montserrat">Create an account</h2>
          <p className="mb-3">Let's get started for free</p>
          <div className="form-group d-flex flex-column gap-2 justify-content-center">
            <input
              type="text"
              className="form-control"
              placeholder="Username"
              name="name"
              id="name"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
            />
            {formik.errors.name && formik.touched.name ? <p className='alert alert-danger'>{formik.errors.name}</p> : ''}

            <input
              type="email"
              className="form-control"
              placeholder="Email"
              name="email"
              id="email"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
            />
            {formik.errors.email && formik.touched.email ? <p className='alert alert-danger'>{formik.errors.email}</p> : ''}

            <input
              type="password"
              className="form-control"
              placeholder="Password"
              name="password"
              id="password"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
            />
            {formik.errors.password && formik.touched.password ? <p className='alert alert-danger'>{formik.errors.password}</p> : ''}

            <input
              type="text"
              inputMode="numeric"
              className="form-control"
              placeholder="Age"
              name="age"
              id="age"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
            />
            {formik.errors.age && formik.touched.age ? <p className='alert alert-danger'>{formik.errors.age}</p> : ''}

            <input
              type="tel"
              inputMode="numeric"
              className="form-control"
              placeholder="phone"
              name="phone"
              id="phone"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
            />
            {formik.errors.phone && formik.touched.phone ? <p className='alert alert-danger'>{formik.errors.phone}</p> : ''}

            <button type="submit" className="btn btn-main">
            {loading ? <i className='fa-solid fa-spinner fa-spin'></i> : 'Register' }
            </button>
           
          </div>
        </form>
      </div>
    </section>



  
  
  
  </>
}
