import React, { useEffect } from 'react'
import { useFormik } from 'formik';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import toast from 'react-hot-toast';
import * as yup from 'yup'
import { useState } from 'react';
import axios from 'axios';
import Swal from 'sweetalert2';
export default function Note({note,deleteNote,updateNotes ,displaynote }) {
    
   let {_id}=note
   



    //delete

    function deleteNote(id){
      const swalWithBootstrapButtons = Swal.mixin({
        customClass: {
          confirmButton: ' mx-2 btn btn-success',
          cancelButton: 'btn btn-danger'
        },
        buttonsStyling: false
      })
      
      swalWithBootstrapButtons.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        cancelButtonText: 'No, cancel!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          deleteNoteAction(id)
          swalWithBootstrapButtons.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          )
        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
          swalWithBootstrapButtons.fire(
            'Cancelled',
            'Your imaginary file is safe :)',
            'error'
          )
        }
      })
    }
  
    async function deleteNoteAction(id){

      try {
  
        let {data}=await axios.delete(`https://note-sigma-black.vercel.app/api/v1/notes/${id}`,{
          headers:{
            token:`3b8ny__${localStorage.getItem('token')}`
          }
        })
        console.log(data)
        if(data.msg=='done'){
          displaynote()
          toast.error('Note Removed Successfully')
        }
        
      } catch (error) {
        console.log(error)
      }
  
    }


    async function updateNotes(val){
        try {
    
          let {data}=await axios.put(`https://note-sigma-black.vercel.app/api/v1/notes/${note._id}`,val,{
            headers:{
              token:`3b8ny__${localStorage.getItem('token')}`
            }
          })
    
          console.log(data)
          displaynote()
          
        } catch (error) {
          console.log(error.response.data.msg);
        }
      }

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let validationSchema= yup.object({
       
        title:yup.string().required('title is required'),
     
       content:yup.string().required('contant is required')
    
    })

    let formik = useFormik({
        initialValues:{
          title:note.title,
          content:note.content,
        },
        validationSchema,
        onSubmit:updateNotes,
    
      })

     


  return <>

<Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Update Note</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <form action="">
            <input defaultValue={note.title} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-4' type="text" name='title' id='title' placeholder='Enter Title' />
            {formik.errors.title && formik.touched.title ? <p>{formik.errors.title}</p>:''}
            <input defaultValue={note.content}  onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control' type="text" name='content' id='content' placeholder='Enter Content' />
            {formik.errors.content && formik.touched.content ? <p>{formik.errors.content}</p>:''}
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={function(){
            handleClose()
            formik.handleSubmit()
          }}>
            Update Note
          </Button>
        </Modal.Footer>
      </Modal>









<div key={note._id} className='col-md-3 col-sm-6'>

<div className='itm text-center mt-4 py-4 position-relative '>
<i class="fa-solid fa-thermometer position-absolute top-0 end-0 text-danger fs-4 pen"></i>
  <h2>{note.title}</h2>
  <p>{note.content}</p>

  <div className='icons d-flex justify-content-around '>
  <i onClick={()=>deleteNote(note._id)} className="fa-solid fa-trash-can rmv"></i>
  <i onClick={handleShow} className="fa-solid fa-file-pen edt"></i>
  </div>
</div>

</div>

  
  
  </>
}
